<template>
  <div id="app">
    <app-header></app-header>
    <router-view></router-view>
    <!-- <div>
      <button @click="setLang(lang)" v-for="lang in locales" :disabled="isLang(lang)">{{ $t('locales.'+ lang) }}</button>
    </div>
    <hr>
    <div>
      <button @click="increment">{{ $tc('messages.counter', countPlural, { n: $store.state.count }) }}</button>
    </div> -->
  </div>
</template>

<script>
import store from 'store'
import { mapActions, mapGetters } from 'vuex'
import AppHeaderComponent from 'components/Header'
export default {
  store,
  computed: mapGetters([
    'countPlural'
  ]),
  methods: {
    ...mapActions([
      'increment'
    ]),
    onLangClick (lang) {
      this.setLang(lang) // mixin method
      this.increment() // store action
    }
  },
  components: {
    'app-header': AppHeaderComponent
  }
}
</script>





